@import url(https://fonts.googleapis.com/css?family=Montserrat:500);

.hero{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: black;
    width: 100vw;
    height: calc(100vh - 80px);
    overflow: hidden;
}

.myVideo {
    min-height: 100%;
    opacity: 0.4;
}

.hero-text{
    font-size: calc(1vh + 1vw);
    color: #fff;
    position: absolute;
    align-items: center;
    text-align: center;
}

.hero-text > p {
    font-size: calc(0.8vh + 0.8vw)
}
.center{
    padding: 100px 0 50px 0;
    text-align: center;
    width: 100vw;
    font-size: calc(2vh + 2vw)
}

.contents{
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 100px 100px 100px;
}

.card{
    background-color: aliceblue;
    min-width: 300px;
    max-width: 400px;
    min-height: 400px;
    border-radius: 50px;
    margin: 30px;
    cursor: pointer;
}

.card-image{
    overflow: hidden;
    height: 300px;
    border-radius: 50px;
}

.card-image img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card-title{
    width: 50%;
    height: 50px;
    background-color:#fff;
    border-radius: 50px;
    transform: translate(50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.card-desc{
    padding: 0 30px 20px 30px;
    word-break: keep-all;
    text-align: justify;
    transform: translate(0, -20px);
}

.card:hover{
    scale: 1.1;
    box-shadow: 10px 10px 27px -22px rgba(0,0,0,0.75);
    transition: scale linear .5s, box-shadow linear .5s;
}

.card img:hover{
    scale: 1.8;
    transition: scale ease-in-out 1.5s;
}

.card img:not(:hover){
    scale: 1;
    transition: scale ease-in-out 1.5s;
}

.card:not(:hover){
    scale: 1;
    box-shadow: none;
    transition: scale linear .5s, box-shadow linear .5s;
}
.allScreen{
    position: absolute;
    top: 100px;
    width: 100%;
    height: 100%;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.messageBox div{
    height: 100%;
}

.messageBox{
    margin-top: 20px;
    width: 75%;
    max-width: 800px;
    height: 30px;
    border-radius: 30px;
    background-color:#D4EDDA;
    border: solid 1.5px #1a5729bb;
    display: flex;
    flex-wrap: wrap;
    color: #1A5729;
}

.messageText{
    width: 80%;
    display: flex;
    padding: 10px;
    align-items: center;
}

.messageButton{
    width: 20%;
    display: flex;
    justify-content: right;
}

.messageButton button{
    height: 100%;
    width: auto;
    padding: 0 10px 0 10px;
    border-radius: 30px;
    background-color: #D4EDDA;
    color:#1A5729;
    border: solid #1A5729 1px;
}

@media (max-width: 600px){
    .messageBox {
        width: 90%;
    }
}

@media (min-width: 601px){
    .messageBox {
        width: 75%;
    }
}

.container{
    width: 60%;
    height: 200px;
    margin: 10px;
    border: 2px black solid;
    box-shadow: 5px 5px 0px 0px rgba(0,0,0,0.75);
    text-align: center;
    overflow: hidden;
}

.container-first{
    transform: translate(100px);
}

.container-second{
    transform: translate(-100px);
}

.container-first img, .container-second img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    scale: 1.1;
    -webkit-filter: blur(1px);
            filter: blur(1px);
}

.container-text{
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.container:hover{
    cursor: pointer;
    scale: 1.02;
    transition: ease-in-out 0.3s scale;
}

.container:not(:hover){
    scale: 1;
    transition: ease-in-out 0.3s scale;
}

@media (max-width: 1000px){
    .container-first, .container-second{
        transform: none;
        width: 100%;
    }

    .contents {
        padding: 0 20px 100px 20px;
    }
}

@media (min-width: 1001px){
    .container-first{
        transform: translate(100px);
    }
    
    .container-second{
        transform: translate(-100px);
    
    }
}
.formRepair{
    display: block;
    width: 70vw;
    max-width: 1000px;
    border-radius: 10px;
    border: solid black 1px;
    margin-bottom: 100px;
}

.contentsRepair{
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}

.boxFormRepair{
    width: 100%;
    padding: 0px 20px 20px 20px;
}

.boxFormRepair button{
    width: 100%;
    height: 30px;
    background-color: #fff;
    border-radius: 10px;
    border: black solid 1px;
}

.boxFormRepair button:hover{
    background-color: #378194;
    color: #fff;
    transition: background-color linear 0.3s, color linear 0.3s;
}

.boxFormRepair button:not(:hover){
    background-color: #fff;
    color: black;
    transition: background-color linear 0.3s, color linear 0.3s;
}

.boxFormRepair input{
    display: block;
    width: 100%;
    height: 30px;
    border-style: none;
    border-bottom: solid black 1px;
    padding-left: 10px;
    margin-top: 5px;
}

.contentsRepair input:focus, .contentsRepair textarea:focus{
    border-bottom: solid #29a5c4 2px;
}

.boxFormRepair textarea{
    width: 100%;
    border-style: none;
    border-bottom: solid black 1px;
    padding-left: 10px;
    margin: 10px 0 10px 0;
    resize: vertical;
    color: rgba(0, 0, 0, 0.562);
    -webkit-user-select: auto;
        -ms-user-select: auto;
            user-select: auto;
}

.sectionFormTitle{
    width: 100%;
    text-align: center;
    padding: 30px;

}

.boxFormRepairDuplo{
    display: flex;
    flex-wrap: wrap;
}

.boxFormRepairDuploItem{
    width: 50%;
    padding: 0px 20px 20px 20px;
}

.boxFormRepairDuploItem input{
    display: block;
    width: 100%;
    height: 30px;
    border-style: none;
    border-bottom: solid black 1px;
    padding-left: 10px;
    margin-top: 5px;
}

@media (max-width: 600px){
    .boxFormRepairDuploItem {
        width: 100%;
    }

    .formRepair{
        width: 90%;
    }
}

@media (min-width: 601px){
    .boxFormRepairDuploItem {
        width: 50%;
    }

    .formRepair{
        width: 70%;
    }
}



.nav {
    background-color: rgba(0, 0, 0, 0.952);
    width: 100vw;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 5% 10px 10%;
}

.nav img, .nav a, .nav Link {
    height: 100%;
    cursor: pointer;
}

.nav img:hover{
    scale: 1.2;
    transition: scale 0.3s linear;
}

.nav img:not(:hover){
    scale: 1;
    transition: scale 0.3s linear;
}

.navlinks {
    list-style: none;
}

.navlinks li {
    display: inline-block;
    padding: 0 20px;
}

.navlinks li a:hover{
    color: #29a5c4;
    transition: color 0.3s linear;
}

.navlinks li a:not(:hover){
    color: white;
    transition: color 0.3s linear;
}

.products {
    background-color: rgba(0, 0, 0, 0.959);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100vw;
    padding: 2px;
}

.menu {
    display: none;
    list-style: none;
    position: absolute;
    background-color: #0f0f0f;
    top: 80px;
    left: 0px;
    width: 100vw;
    padding: 20px;
    z-index: 2;
}

.menu div {
    width: 100%;
    height: 40px;
    padding: 15px;
    border-bottom: solid #fff 1px;
}

.collapse {
    background-color: #0f0f0f;
    border-style: solid 1px #fff;
    border-radius: 10px;
    color: #fff;
    padding: 10px;
    outline: none;
}

.menu div a:hover{
    color: #29a5c4;
    transition: color 0.3s linear;
}

.menu div a:not(:hover){
    color: white;
    transition: color 0.3s linear;
}

@media (max-width: 600px){
    .nav .navlinks {
        display: none;
    }
}

@media (min-width: 601px){
    .collapse {
        display: none;
    }
}
.footer{
    background-color: #141414;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100vw;
    height: 30vh;
    padding: 0 10% 0 10%;
}

.footer-center{
    width: 30%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-center img, .footer-center a {
    height: 80%;
}

.footer-end{
    color: #fff;
    background-color: #0f0f0f;
    font-size: 80%;
    padding: 3px;
    width: 100vw;
    min-height: 20px;
    text-align: center;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  scroll-behavior: smooth;
  outline: none;
}

li {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: rgb(212, 212, 212);
}

a {
  text-decoration: none;
  color: black;
}



