@import url('https://fonts.googleapis.com/css?family=Montserrat:500');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  user-select: none;
  scroll-behavior: smooth;
  outline: none;
}

li {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: rgb(212, 212, 212);
}

a {
  text-decoration: none;
  color: black;
}


